.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/**********************************************   N A V B A R **************************************/
:root {
  --primary-white: #fff;
  --primary-purple: #553d67;
  --primary-dark: rgba(255, 255, 255, 0);
  --primary-red: #f5b506;
  --primary-hover-red: #f5ae14;
  --primary-grey: #99738e;
  --primary-darkblue: #1e1258;
  --primary-blue: #281376;
}

.navbar {
  min-height: 100px;
  background-color: var(--primary-darkblue) !important;
}

nav {
  box-shadow: 2px 2px 12px rgb(0, 0, 0, 0.2);
}

nav ul {
  display: flex;
}

nav a, nav p {
  margin: 30px;
  font-size: 20px;
  font-weight: 50;
  font-family: 'Montserrat';
  color: var(--primary-white) !important;
}

nav p {
  font-stretch: wider;
}

.navbar-nav > li > a {
  border-top: 3px solid var(--primary-dark);
}

nav a.nav link {
  cursor: pointer;
}

.navbar-nav > li > a.active, .navbar-nav > li > a.active:focus {
  border-top: 3px solid;
}

.navbar-nav > li > a:hover {
  border-top: 3px solid var(--primary-red);
}

/********************************************** H E A D E R **************************************/
.header-wrapper {
  position: relative;
  background: url(background-image2.png);
  background-size: cover;
  background-position: center;
  height: 90vh;
}

.color-overlay {
  width: 100%;
  height: 100%;
  background: var(--primary-blue);
  opacity: .7;
  position: relative;
}

.main-info {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family: 'Montserrat';
}

.main-info h1 {
  color: var(--primary-white);
  text-transform: uppercase;
}

/********************************************** T Y P E D  T E X T **************************************/
.typed-text {
  font-size: 2rem;
  color: var(--primary-white);
}

.btn-main-offer {
  border: 1px solid var(--primary-red);
  text-decoration: none;
  text-transform: uppercase;
  border-radius: 0;
  padding: 10px 12px 12px 12px;
  color: var(--primary-white);
  margin: 2rem;
  font-family: 'Montserrat';
}

.btn-main-offer:hover {
  text-decoration: none;
  background-color: var(--primary-hover-red);
  color: var(--primary-white);
  transition: .2s ease-out;
}

/********************************************** A B O U T **************************************/
.photo-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
}

.about-info {
  color: var(--primary-darkblue);
  text-transform: uppercase;
  font-family: 'Montserrat';
}

.about-paragraph {
  color: var(--primary-blue);
  font-family: 'Montserrat';
}

.profile {
  width: 26rem;
  height: 26rem;
  border-radius: 56%;
  object-fit: cover;
  padding: .3rem;
  border: 3px solid #282c34
}

@media(max-width: 468px) {
  .profile {
    width: 10rem;
    height: 10rem;
  }
}
/********************************************** S E R V I C E S **************************************/
.service {
  text-align: center;
  padding-bottom: 2rem;
  background-color: var(--primary-blue);
  font-family: 'Montserrat';
}

.service-header {
  color: var(--primary-white);
  text-transform: uppercase;
  font-family: 'Montserrat';
}

.service .circle {
  position: relative;
  margin: 0.375rem auto;
  background-color: var(--primary-darkblue);
  border-radius: 50%;
  width: 3.25rem;
  height: 3.25rem;
}

.service .icon {
  font-size: 1.4rem;
  position: absolute;
  color: var(--primary-white);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.service .box {
  height: 16rem;
  border-bottom: 0.3125rem solid var(--primary-blue);
  padding: 0.625rem;
  margin-bottom: 1.875rem;
  background-color: var(--primary-white);
  transition: 0.3s ease-in-out;
}

.service .box:hover {
  background-color: var(--primary-darkblue);
  border-bottom: black;
  color: var(--primary-white);
}

.service .box:hover .icon {
  color: black;
}

.service .box:hover .circle {
  background-color: var(--primary-white);
}


/********************************************** W O R K  S E C T I O N **************************************/

.work-header {
  color: var(--primary-darkblue);
  text-transform: uppercase;
  font-family: 'Montserrat';
}

.work-wrapper {
  padding: 3rem 0;
}

.work-image {
  width: 26rem;
  height: 16rem;
  border: 1px solid var(--primary-blue);
  padding: 0 0.5rem;
}

.work-image-box {
  position: relative;
  margin: 1rem;
}

.work-title-text {
  font-family: 'Montserrat';
  color: var(--primary-blue);
  font-size: 25px;
  padding: 5px;
  font-weight: bolder;
}

.work-image-box:hover {
  display: inline-block;
  vertical-align: middle;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-property: transform;
  transition-property: transform;
}

.work-image-box:hover, .work-image-box:focus, .work-image-box:active {
  -webkit-transform: scale(0.9);
  transform: scale(0.9);
}

/********************************************** O V E R F L O W **************************************/
.overflow {
  position: absolute;
  opacity: 0;
  background: black;
  width: 26rem;
  height: 16rem;
  top: 0;
  border: 1px solid var(--primary-red);
  cursor: pointer;
}

.work-image-box:hover .overflow{
  opacity: 60%;
  transition: 0.3s ease-in-out;
}

.work-icon {
  position: absolute;
  opacity: 0;
  margin-left: -14rem;
  margin-top: 6.5rem;
  color: var(--primary-hover-red);
  font-size: 2.5rem;
}

.work-image-box:hover .work-icon {
  opacity: 70%;
  transition: 0.3s;
}

.popupbox-wrapper {
  margin-top: 10rem;
}

/********************************************** O V E R F L O W  B O X**************************************/
.work-image-popupbox {
  width: 45rem;
  padding: 0 0.5rem;
  height: 350px;
}

.popup-box-font {
  font-family: 'Montserrat';
  padding: 0.5rem;
  width: 45rem;
}

.popupbox-titleBar {
  font-family: 'Montserrat';
  color: var(--primary-blue);
}

.popupbox-btn--close {
  content: 'x';
  color: black !important;
  cursor: pointer;
  background: rgba(155, 155, 155, 0.733) !important;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  line-height: 20px !important;
  text-align: center;
  border: 1px solid #999;
  font-size: 20px;
}

.hyper-link {
  cursor: pointer;
  color: var(--primary-blue);
  font-family: 'Montserrat';
}

.hyper-link:hover {
  cursor: pointer;
  color: var(--primary-red);
}

@media(max-width: 1120px) {
  .work-image-popupbox {
    width: 100%;
    height: 100%;
  }

  .popup-box-font {
    width: 100%;
  }
}

@media(max-height: 570px) {
  .popupbox-wrapper {
    height: 100%;
  }

  .work-image-popupbox {
    width: 50%;
  }
}

@media(max-height: 280px) {
  .popupbox-wrapper {
    height: 100%;
  }

  .work-image-popupbox {
    width: 30%;
  }
}

/********************************************** C O N T A C T **************************************/
.contact {
  background-color: var(--primary-blue);
  padding: 3rem 0;
}

.contact h1 {
  color: var(--primary-white);
  padding: 1.3rem 0;
  text-transform: uppercase;
}

.contact p {
  color: var(--primary-white);
  font-size: (1);
}

/********************************************** C O N T A C T **************************************/
.contact input, .contact textarea {
  width: 100%;
  box-sizing: border-box;
  background: none;
  resize: none;
  border: 0;
  border-bottom: 2px solid var(--primary-white);
  border-radius: 0;
  color: var(--primary-white);
  outline: 0 !important;
  box-shadow: none !important;
  top: 0;
  margin-bottom: 1rem;
  padding-left: 0 !important;
}

.contact .container {
  padding: 4rem 0;
}

.contact input:focus, .contact textarea:focus {
  background: transparent;
  color: var(--primary-white);
  border-bottom: 0.125rem solid var(--primary-grey);
}

.contact textarea {
  height: 12.2rem;
}

.contact-btn {
  margin: 1rem 0 0 0;
  width: 100%;
  background: none;
}
.line {
  position: relative;
  top: -0.8rem;
  width: 0;
  height: 0.125rem;
  background: var(--primary-red);
  display: inline-block;
}

input:focus+ .line, textarea:focus+ .line {
  width: 100%;
  transition: width 0.5s ease-in-out;
}

input.form-control {
  margin: -0.3rem 0;
}

textarea.form-control {
  margin: -0.3rem 0;
}

.error-message {
  color: var(--primary-white);
  background: var(--primary-red);
  text-align: left;
}

.success-message {
  font-weight: 900;
  color: var(--primary-darkblue);
}

/********************************************** M E D I A **************************************/
@media(max-width: 768px) {
  .contacts {
    overflow-x: hidden;
  }
  
  .contacts .container {
    padding: 0 1rem;
  }

  .contacts p {
    padding: 0 0.4rem;
  }
}

/********************************************** F O O T E R **************************************/
.footer {
  background: var(--primary-darkblue);
  color: var(--primary-white);
  padding: 3rem;
}

.footer a {
  text-decoration: none;
  color: var(--primary-white);
}

.footer a:hover {
  text-decoration: none;
  color: var(--primary-red);
  cursor: pointer;
}

.footer button {
  border: 1px solid var(--primary-blue);
  text-decoration: none;
  text-transform: uppercase;
  border-radius: 0;
  color: var(--primary-blue);
}

.footer button:hover {
  text-decoration: none;
  background-color: var(--primary-red);
  color: var(--primary-white);
  transition: .2s ease-out;
}

.footer {
  margin-bottom: 0;
}

.social {
  margin: 0 1rem;
  transition: transform 250ms;
  display: inline-block;
}

.social:hover {
  transform: translateY(-2px);
}